.App {
  font-family: sans-serif;
  text-align: center;
}
.circle {
  height: 90px;
  width: 90px;
  border-radius: 50%;
  border: 8px solid blue;
  box-sizing: border-box;
}
.cross {
  width: 90px;
  height: 90px;
  position: relative;
  transform: rotate(45deg);
}
.cross:before,
.cross:after {
  position: absolute;
  content: "";
  background-color: red;
}
.cross:before {
  left: 50%;
  width: 30%;
  height: 100%;
  margin-left: -15%;
}
.cross:after {
  top: 50%;
  width: 100%;
  height: 30%;
  margin-top: -15%;
}
